  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  min-width: 320px;
  overflow-x: hidden;
  font-family: "proximaNova", sans-serif;
  font-weight: 400; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden; }

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0px 15px;
  width: 100%; }

.content {
  flex: 1 1 auto; }

.header {
