  width: 100%;
  position: relative; }
  .header:before {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: 0.9; }
  .header__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    padding: 45px;
    z-index: 1;
    position: relative; }
    @media (max-width: 480px) {
      .header__content {
        padding: 30px; } }
  .header__top {
    flex: 0 1 auto; }
  .header__logo {
    display: flex;
    align-items: center; }
  .header__logo-text {
    padding: 0px 0px 0px 10px; }
  .header__title {
    font-size: 21px;
    letter-spacing: 1px;
    color: #ffffff;
    font-weight: 700;
    font-family: "PlayfairDisplay";
    text-transform: uppercase; }
  .header__subtitle {
    font-size: 9px;
    letter-spacing: 0px;
    color: #ffffff; }
  .header__menu {
    transform: translateX(-202px); }
    @media (max-width: 1191px) {
      .header__menu {
        transform: translateX(0px); } }
  .header__list {
    display: flex;
    margin: 0px 0px 0px -20px; }
  .header__item {
    list-style-type: none;
    margin: 0px 20px; }
  .header__link {
    font-size: 14px;
    letter-spacing: 0px;
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.3s ease-in; }
    .header__link:hover {
      color: #e3a062; }
  .header__sign {
    font-size: 14px;
    letter-spacing: 0px;
    color: #ffffff;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s ease-in; }
    .header__sign:hover {
      color: #e3a062; }
  .header__middle {
    flex: 1 1 auto;
    position: relative;
    display: flex;
    align-items: center; }
    @media (max-width: 980px) {
      .header__middle {
        display: flex;
        align-items: center;
        height: 100%; } }
  .header__slider {
    min-width: 0;
    max-width: 590px;
    margin: 0 auto; }
    @media (max-width: 980px) {
      .header__slider {
        max-width: 490px; } }
    @media (max-width: 860px) {
      .header__slider {
        max-width: 390px; } }
    @media (max-width: 660px) {
      .header__slider {
        max-width: 290px; } }
  .header__slider-item {
    margin: 0 auto;
    padding: 5px; }
  .header__headline {
    max-width: 590px;
    font-size: 120px;
    letter-spacing: 2px;
    line-height: 120px;
    color: #e3a062;
    font-weight: 700;
    font-family: "PlayfairDisplay"; }
    @media (max-width: 980px) {
      .header__headline {
        font-size: 80px;
        line-height: 80px; } }
    @media (max-width: 767px) {
      .header__headline {
        font-size: 60px;
        line-height: 60px; } }
    @media (max-width: 580px) {
      .header__headline {
        margin: 20px 0px; } }
    @media (max-width: 320px) {
      .header__headline {
        font-size: 40px;
        line-height: 40px; } }
  .header__descr {
    max-width: 390px;
    font-size: 16px;
    letter-spacing: 0px;
    line-height: 24px;
    color: #ffffff; }
  .header__btn {
    margin: 66px 0px 0px 0px; }
    @media (max-width: 580px) {
      .header__btn {
        margin: 26px 0px 0px 0px; } }
  .header__bottom {
    flex: 0 1 auto; }
  .header__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    flex-wrap: wrap; }
  .header__social-list {
    display: flex; }
  .header__social-item {
    list-style-type: none; }
    .header__social-item:nth-child(3) .i {
      visibility: hidden; }
  .header__social-link {
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0px;
    color: #ffffff;
    transition: all 0.3s ease-in; }
    .header__social-link:hover {
      color: #e3a062; }
  .header__dots {
    display: flex;
    align-items: center;
    font-size: 26px;
    letter-spacing: 1px;
    color: #ffffff;
    font-weight: 700;
    font-family: "PlayfairDisplay";
    transform: translateX(-242px); }
    @media (max-width: 1191px) {
      .header__dots {
        display: none; } }
  .header__start {
    cursor: pointer;
    transition: all 0.3s ease-in; }
    .header__start:hover {
      color: #e3a062; }
  .header__line {
    background-color: #ffffff;
    height: 1px;
    width: 164px;
    margin: 0px 20px; }
  .header__end {
    cursor: pointer;
    transition: all 0.3s ease-in; }
    .header__end:hover {
      color: #e3a062; }
  @media (max-width: 362px) {
    .header__scroll-down {
      margin: 20px 0px 0px 0px; } }
  .header__scroll-btn {
    font-size: 14px;
    letter-spacing: 0px;
    color: #ffffff;
    text-decoration: none; }

.read-more {
  font-size: 14px;
  letter-spacing: 1px;
  color: #e3a062;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0px 0px 9px 0px;
  border-bottom: 1px solid #e3a062;
  display: inline-block;
  margin: 0px 0px 5px 0px; }

.i {
  font-size: 20px;
  vertical-align: middle;
  padding: 0px 10px 0px 10px;
  color: #ffffff; }

.header__slider-prev,
.header__slider-next {
  font-size: 14px;
  letter-spacing: 0px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  position: relative; }

.header__slider-prev,
.header__slider-next {
  position: absolute;
  top: 50%; }
  @media (max-width: 580px) {
    .header__slider-prev,
    .header__slider-next {
      top: auto;
      bottom: 50px; } }
  @media (max-width: 320px) {
    .header__slider-prev,
    .header__slider-next {
      bottom: 36px; } }

.header__slider-next {
  right: 0; }

.header__slider-prev::before {
  position: absolute;
  content: "";
  width: 47px;
  height: 2px;
  background-color: #e3a062;
  top: 50%;
  right: -70px; }
  @media (max-width: 767px) {
    .header__slider-prev::before {
      width: 27px;
      right: -40px; } }

.header__slider-next::after {
  position: absolute;
  content: "";
  width: 47px;
  height: 2px;
  background-color: #e3a062;
  top: 50%;
  left: -70px; }
  @media (max-width: 767px) {
    .header__slider-next::after {
      width: 27px;
      left: -40px; } }

.header {
  position: relative;
  width: 100%;
  top: 0;
  left: 0; }

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0; }

.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 4; }

.header__logo {
  position: relative;
  z-index: 4;
  overflow: hidden; }

.header__logo img {
  max-width: 100%;
  display: block;
  height: auto; }

.header__burger {
  display: none; }

@media (max-width: 1191px) {
  .header__sign {
    display: none; }
  .header__list {
    margin: 0px 0px 0px 0px; }
  body.lock {
    overflow: hidden; }
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 4; }
  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s; }
  .header__burger:before {
    top: 0; }
  .header__burger:after {
    bottom: 0; }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px; }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px; }
  .header__burger.active span {
    transform: scale(0); }
  .header__burger span {
    position: absolute;
    background-color: #fff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s; }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.9);
    transition: all 0.3s ease 0s;
    z-index: 3; }
  .header__menu.active {
    top: 0;
    padding: 45px; } }
  @media (max-width: 1191px) and (max-width: 480px) {
    .header__menu.active {
      padding: 30px; } }

@media (max-width: 1191px) {
  .header__list {
    flex-direction: column;
    position: absolute;
    top: 120px; }
  .header__list li {
    margin: 0px 0px 20px 0px; }
    .header__list li:last-child {
      margin: 0px 0px 0px 0px; }
  .header__link {
    font-size: 32px; } }
