.header {
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
}
.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
}
.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 4;
}
.header__logo {
  position: relative;
  z-index: 4;
  overflow: hidden;
}
.header__logo img {
  max-width: 100%;
  display: block;
  height: auto;
}
.header__burger {
  display: none;
}

@media (max-width: 1191px) {
  .header__sign {
    display: none;
  }
  .header {
    &__list {
      margin: 0px 0px 0px 0px;
    }
  }
  body.lock {
    overflow: hidden;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 4;
  }
  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }
  .header__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger span {
    position: absolute;
    background-color: #fff;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }
  .header__menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto; // для возмржности скрола в горизонтальном положении
    background-color: rgba(0, 0, 0, 0.9);
    // padding: 70px 10px 20px 10px;
    transition: all 0.3s ease 0s;
    z-index: 3;
  }
  .header__menu.active {
    top: 0;
    padding: 45px;
    @media (max-width: 480px) {
      padding: 30px;
    }
  }
  .header__list {
    flex-direction: column;
    position: absolute;
    top: 120px;
  }
  .header__list li {
    margin: 0px 0px 20px 0px;
    &:last-child {
      margin: 0px 0px 0px 0px;
    }
  }
  .header__link {
    font-size: 32px;
  }
}
